@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
:root {
  --headlines: "Permanent Marker", cursive;
  --primary-color: #3a4052;
  --red: #990000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  text-align: center;
  line-height: 1.5;
  background-color: gainsboro;
}

h1 {
  font-weight: 300;
  font-size: 60px;
  line-height: 1.2;
  margin-bottom: 15px;
  color: #990000;
  color: var(--red);
  font-family: "Permanent Marker", cursive;
  font-family: var(--headlines);
}

.showcase {
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0, 20px;
  color: #990000;
  color: var(--red);
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80%;
  overflow: hidden;
}

.video-container video {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.content {
  z-index: 10;
}

h2 {
  font-family: "Permanent Marker", cursive;
  font-family: var(--headlines);
  color: #990000;
  color: var(--red);
}

.headline {
  font-family: "Permanent Marker", cursive;
  font-family: var(--headlines);
  margin: 30px;
}

a {
  color: #990000;
  color: var(--red);
  font-family: "Permanent Marker", cursive;
  font-family: var(--headlines);
}

a :hover {
  background-color: #3a4052;
  background-color: var(--primary-color);
  color: black;
}

img {
  width: 150px;
  height: 140px;
}

/* //////// TOOLS CSS////// */
.toolsList {
  background-color: #d1ecf1;
}

button {
  margin: 5px;
}

input {
  margin: 5px;
  width: 200px;
}

.button-group {
  width: 80%;
  justify-content: space-between;
  border: solid black;
}

.main-buttons {
  background-color: grey;
}

.btn {
  color: #990000;
  color: var(--red);
  width: 200px;
  border: 5px black;
}

