/*////////////DASHBOARD///////////////////*/

.dash-board {
  width: 70%;
  margin: 13%;
  border: solid #3a4052;
  border: solid var(--primary-color);
  padding: 5%;
}

.dashboard-btn {
  padding: 10px;
  margin: 10px;
  border: solid black;
  background-color: gray;
  color: #990000;
  color: var(--red);
  font-size: 20px;
  font-weight: bold;
}
.dashboard-btn:hover {
  background-color: #3a4052;
  background-color: var(--primary-color);
}

:root {
    --headlines: 'Permanent Marker', cursive;
    --primary-color: #3a4052;
    --red: #990000;
}


.btn-game {
    padding: 10px;
    margin: 20px;
    color: #990000;
    color: var(--red);
    background-color: grey;
    border: solid black;
