}

.tic-tac-toe-message {
    margin: 50px;
    text-align: right;
    font-size: 25px;
    font-family: 'Permanent Marker', cursive;
    font-family: var(--headlines);
    color: #990000;
    color: var(--red);
    padding: 70px 0;
}

.samuraj {
    margin: 50px;
}

.board {
    margin: auto;
}
.like {
    opacity: 0.5;
}

.like:hover {
    opacity: 1;
}
.all-lists-of-fetures {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 50px;
}

.one-list-of-features {
  height: 400px;
  width: 400px;
  border: solid black 4px;
  padding: 50px;
}
.all-lists-of-texts {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin: 50px;
}
.one-list-of-text {
  width: 100%;
  height: 200px;
  border: solid black 4px;
  padding: 50px;
}

@media (max-width: 768px) {
  .all-lists-of-fetures {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: 100px;
  }
}

